@import url(https://fonts.googleapis.com/css2?family=Ruthie&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/Justin-Sykes-React-Portfolio/static/media/glitter-lights-spots-dots-background-2560x1440.9c6f93f6.jpg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  padding-left: 20px;
}

h2 {
  text-align: center;
}

h6 {
  padding: 20px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.m7 {
  margin-left: 10% !important;
}

.brand-logo {
  background-color: rgb(0, 119, 255);
  margin: 10px;
}

.card {
  margin-left: 30px;
}

.card-title {
  color: black !important;
}
.submit-button {
  text-align: center;
}

#contact-form {
  background-color: white;
}

#home-image {
  width: 300px;
  height: 300px;
}

img {
  float: left;
  margin-right: 20px;
}

.card-action {
  font-size: 50px;
}

#AM-title-block {
  margin-top: 80px;
}
#contact-form {
  margin-top: 20px;
  background-color: transparent;
}

#port-container {
  margin-top: 10%;
}

a.brand-logo.center {
  font-family: "Ruthie", cursive;
  font-weight: bold;
  font-size: xxx-large;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 20%;
}

em {
  font-size: x-large;
}
.nav-link {
  font-size: x-large;
}

